// ==============================
// Custom style
// 自定义样式
// ==============================

// 现代化配色方案
:root {
  --primary-color: #667eea;
  --primary-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --secondary-color: #f093fb;
  --accent-color: #4facfe;
  --text-primary: #2d3748;
  --text-secondary: #718096;
  --bg-primary: rgba(255, 255, 255, 0.9);
  --bg-secondary: #f8fafc;
  --bg-tertiary: #e2e8f0;
  --border-color: rgba(255, 255, 255, 0.2);
  --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --border-radius: 16px;
  --border-radius-sm: 12px;
  --glass-bg: rgba(255, 255, 255, 0.1);
  --glass-border: rgba(255, 255, 255, 0.2);
}

[data-theme="dark"] {
  --text-primary: #f7fafc;
  --text-secondary: #cbd5e0;
  --bg-primary: rgba(45, 55, 72, 0.9);
  --bg-secondary: #1a202c;
  --bg-tertiary: #2d3748;
  --border-color: rgba(255, 255, 255, 0.1);
  --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.5), 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.5), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.5), 0 4px 6px -2px rgba(0, 0, 0, 0.3);
  --glass-bg: rgba(45, 55, 72, 0.3);
  --glass-border: rgba(255, 255, 255, 0.1);
}

// 全局样式优化
body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background: var(--bg-secondary);
  color: var(--text-primary);
  line-height: 1.7;
}

// 主容器布局优化 - 增大内容区域，减少两边空白
.wrapper {
  //padding: 0 1rem !important; // 减少左右内边距
  //max-width: 1600px !important; // 最大宽度
  margin: 0 auto !important;
  width: 100% !important;
}

.container {
  padding: 0 1rem !important; // 减少内部内边距
  max-width: 1400px !important; // 内容容器宽度
  margin: 0 auto !important;
  width: 100% !important;
}

// 主页专用更大的容器
.home .container {
  max-width: 1200px !important;
}

// 头部导航栏美化 - 确保不被容器宽度限制
.header {
  background: var(--glass-bg) !important;
  backdrop-filter: blur(20px) saturate(180%) !important;
  -webkit-backdrop-filter: blur(20px) saturate(180%) !important;
  border-bottom: 1px solid var(--glass-border) !important;
  box-shadow: var(--shadow-sm) !important;

  // 头部容器不受限制，确保主题切换图标显示
  .header-wrapper {
    max-width: none !important;
    width: 100% !important;
    padding: 0 2rem !important;
  }

  .header-title {
    font-weight: 700;
    background: var(--primary-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .menu a {
    position: relative;
    transition: all 0.3s ease;
    border-radius: var(--border-radius-sm);
    padding: 10px 18px;
    color: var(--text-primary) !important;

    &:hover {
      background: var(--glass-bg);
      transform: translateY(-1px);
      backdrop-filter: blur(10px);
    }

    &::after {
      content: '';
      position: absolute;
      width: 0;
      height: 2px;
      bottom: 0;
      left: 50%;
      background: var(--primary-gradient);
      transition: all 0.3s ease;
      transform: translateX(-50%);
    }

    &:hover::after {
      width: 80%;
    }
  }

  // 确保主题切换按钮显示
  .theme-switch {
    display: flex !important;
    align-items: center !important;
    margin-left: auto !important;
  }
}

// 主页个人信息区域美化
.home .profile {
  background: transparent !important;
  backdrop-filter: none !important;
  -webkit-backdrop-filter: none !important;
  border: none !important;
  border-radius: 0 !important;
  padding: 4rem 3rem !important;
  box-shadow: none !important;
  margin: 2rem 0 3rem 0 !important;
  position: relative;
  overflow: hidden;

  .avatar {
    position: relative;
    margin-bottom: 1.5rem;

    img {
      border-radius: 50%;
      box-shadow: var(--shadow-md);
      transition: transform 0.3s ease;
      border: 4px solid transparent;
      background: var(--primary-gradient);
      background-clip: padding-box;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  .profile-title {
    font-size: 2.5rem !important;
    font-weight: 800 !important;
    margin-bottom: 0.5rem !important;
    color: var(--primary-color) !important;
    line-height: 1.2 !important;
  }

  .profile-subtitle {
    font-size: 1.2rem !important;
    color: var(--text-secondary) !important;
    margin-bottom: 2rem !important;
    font-weight: 400 !important;
  }
}

// 文章列表卡片美化 - 现代渐变效果
.summary {
  // 现代渐变背景
  background: linear-gradient(135deg,
    rgba(255, 255, 255, 0.95) 0%,
    rgba(248, 250, 252, 0.95) 50%,
    rgba(241, 245, 249, 0.95) 100%
  ) !important;

  // 毛玻璃效果
  backdrop-filter: blur(20px) saturate(180%) !important;
  -webkit-backdrop-filter: blur(20px) saturate(180%) !important;

  // 现代边框渐变
//   border: 1px solid transparent !important;
  background-clip: padding-box !important;
  position: relative !important;

  // 边框渐变效果
  &::before {
    content: '' !important;
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    z-index: -1 !important;
    margin: -1px !important;
    border-radius: inherit !important;
    background: linear-gradient(135deg,
      rgba(102, 126, 234, 0.3) 0%,
      rgba(118, 75, 162, 0.2) 50%,
      rgba(96, 165, 250, 0.3) 100%
    ) !important;
  }

  border-radius: 20px !important;
  box-shadow:
    0 8px 32px rgba(31, 38, 135, 0.15),
    0 4px 16px rgba(31, 38, 135, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.8) !important;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1) !important;
  overflow: hidden !important;
  margin: 1.5rem !important;
  padding: 0 !important;

  // 修复重影问题
  transform-style: flat !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;

  // 防止内容重叠
  isolation: isolate !important;

  // 确保所有文字清晰渲染
  * {
    -webkit-font-smoothing: antialiased !important;
    -moz-osx-font-smoothing: grayscale !important;
    text-shadow: none !important;
  }

  &:hover {
    transform: translateY(-8px) scale(1.02) !important;
    box-shadow:
      0 20px 40px rgba(31, 38, 135, 0.25),
      0 10px 25px rgba(31, 38, 135, 0.15),
      0 0 0 1px rgba(102, 126, 234, 0.4),
      inset 0 1px 0 rgba(255, 255, 255, 0.9) !important;

    // 悬浮时背景渐变变化
    // background: linear-gradient(135deg,
    //   rgba(255, 255, 255, 0.98) 0%,
    //   rgba(249, 250, 251, 0.98) 30%,
    //   rgba(243, 244, 246, 0.98) 70%,
    //   rgba(241, 245, 249, 0.98) 100%
    // ) !important;

    // 悬浮时边框渐变增强
    &::before {
      background: linear-gradient(135deg,
        rgba(102, 126, 234, 0.5) 0%,
        rgba(139, 92, 246, 0.4) 25%,
        rgba(168, 85, 247, 0.4) 50%,
        rgba(118, 75, 162, 0.3) 75%,
        rgba(96, 165, 250, 0.5) 100%
      ) !important;
    }
  }

  .featured-image {
    border-radius: 20px 20px 0 0 !important;
    overflow: hidden !important;
    position: relative !important;

    img {
      transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1) !important;
      filter: brightness(1) contrast(1.05) saturate(1.1) !important;
    }

    &:hover img {
      transform: scale(1.08) !important;
      filter: brightness(1.1) contrast(1.1) saturate(1.2) !important;
    }

    // 现代渐变遮罩，确保标题可读性
    &::after {
      content: '' !important;
      position: absolute !important;
      bottom: 0 !important;
      left: 0 !important;
      right: 0 !important;
      height: 70% !important;
      background: linear-gradient(to top,
        rgba(0, 0, 0, 0.8) 0%,
        rgba(0, 0, 0, 0.6) 30%,
        rgba(0, 0, 0, 0.3) 60%,
        transparent 100%
      ) !important;
      pointer-events: none !important;
      transition: opacity 0.3s ease !important;
    }

    // 悬浮时增强遮罩效果
    &:hover::after {
      background: linear-gradient(to top,
        rgba(0, 0, 0, 0.9) 0%,
        rgba(0, 0, 0, 0.7) 30%,
        rgba(0, 0, 0, 0.4) 60%,
        transparent 100%
      ) !important;
    }
  }

  .summary-content {
    padding: 2.5rem !important; // 增加内边距，让内容更协调
    position: relative !important;
  }

  // 当文章有特色图片时，调整标题位置
  .featured-image + .summary-content {
    margin-top: -3rem !important;
    padding-top: 1rem !important;

    .single-title {
      margin-bottom: 1.5rem !important;
    }
  }

  // 这个样式现在由可点击卡片的.single-title处理，移除重复定义
  .single-title {
    // 基础样式，具体效果由.clickable-card .single-title覆盖
    font-weight: 700 !important;
    font-size: 1.4rem !important;
    margin-bottom: 0.75rem !important;
    color: var(--text-primary) !important;
    line-height: 1.3 !important;
    position: relative !important;
    z-index: 2 !important;
  }

  .post-meta {
    color: var(--text-secondary) !important;
    font-size: 0.9rem !important;
    padding: 0 2rem;
    // margin-bottom: 1rem !important;

    .post-author,
    .post-publish {
      display: inline-flex;
      align-items: center;
      margin-right: 1rem;

      i {
        margin-right: 0.5rem;
        color: var(--primary-color);
      }
    }
  }

  .content {
    color: var(--text-secondary) !important;
    line-height: 1.6 !important;
    // margin-bottom: 1.5rem !important;

    // 优化文字渲染，避免重影
    -webkit-font-smoothing: antialiased !important;
    -moz-osx-font-smoothing: grayscale !important;
    text-rendering: optimizeLegibility !important;

    // 隐藏可能被遮挡的标题，只显示正文内容
    h1, h2, h3, h4, h5, h6 {
      display: none !important;
    }

    // 修复重影问题：使用line-clamp但优化渲染
    display: -webkit-box !important;
    -webkit-line-clamp: 3 !important;
    -webkit-box-orient: vertical !important;
    overflow: hidden !important;
    max-height: 7.2rem !important; // 约4.5行的高度
    position: relative !important;

    // 优化文字渲染，避免重影
    -webkit-font-smoothing: antialiased !important;
    -moz-osx-font-smoothing: grayscale !important;
    text-rendering: optimizeLegibility !important;

    // 为文章摘要内容设置明确的颜色，避免重影
    p, span, div, text {
      color: var(--text-secondary) !important;
      text-shadow: none !important;
      -webkit-font-smoothing: antialiased !important;
    }
  }

  // 底部区域样式 - 确保"阅读全文"和标签完全显示
  .post-footer {
    padding: 1.5rem 2.5rem 2.5rem 2.5rem !important; // 与summary-content保持一致的内边距
    display: flex !important;
    justify-content: space-between !important;
    align-items: flex-end !important;
    flex-wrap: wrap !important;
    gap: 1rem !important;
    border-top: 1px solid var(--bg-tertiary) !important;
    margin-top: auto !important;

    .post-tags {
      display: flex !important;
      flex-wrap: wrap !important;
      gap: 0.5rem !important;
    }

    .read-more {
      white-space: nowrap !important;
      margin-left: auto !important;
    }
  }

  // 如果底部区域不存在，为.summary-content添加足够的底部内边距
  &:not(:has(.post-footer)) .summary-content {
    padding-bottom: 2.5rem !important;
  }
}

// 文章页面美化
.single {
  .single-header {
    background: transparent;
    backdrop-filter: none !important;
    -webkit-backdrop-filter: none !important;
    border: none !important;
    border-radius: 0;
    padding: 2rem;
    margin: 2rem 0; // 移除左右边距
    box-shadow: none;
    position: relative;
    overflow: hidden;

    .single-title {
      font-size: 2.5rem !important;
      font-weight: 800 !important;
      margin-bottom: 1rem !important;
      color: var(--primary-color) !important;
      line-height: 1.2 !important;
    }

    .single-subtitle {
      color: var(--text-secondary) !important;
      font-size: 1.1rem !important;
      margin-bottom: 1.5rem !important;
    }

    .post-meta {
      display: flex !important;
      flex-wrap: wrap !important;
      gap: 1rem !important;
      color: var(--text-secondary) !important;

      .post-author,
      .post-publish,
      .post-edit {
        display: inline-flex !important;
        align-items: center !important;

        i {
          margin-right: 0.5rem !important;
          color: var(--primary-color) !important;
        }
      }
    }
  }

  .content {
    background: transparent !important;
    backdrop-filter: none !important;
    -webkit-backdrop-filter: none !important;
    border: none !important;
    border-radius: 0 !important;
    padding: 1rem !important;
    box-shadow: none !important;
    // margin: 2rem 0 !important; // 移除左右边距

    h1, h2, h3, h4, h5, h6 {
      position: relative !important;
      margin-top: 2rem !important;
      margin-bottom: 1rem !important;
      font-weight: 700 !important;


      &::before {
        content: '' !important;
        position: absolute !important;
        left: -1rem !important;
        top: 50% !important;
        transform: translateY(-50%) !important;
        width: 4px !important;
        height: 100% !important;
        background: var(--primary-gradient) !important;
        border-radius: 2px !important;
      }
    }

    h1 {
      font-size: 2rem !important;
    }

    h2 {
      font-size: 1.75rem !important;
    }

    h3 {
      font-size: 1.5rem !important;
    }

    p {
      margin-bottom: 1.2rem !important;
      line-height: 1.8 !important;
      font-size: 1.05rem !important;
      font-weight: 400 !important;
    }

    img {
      border-radius: var(--border-radius) !important;
      box-shadow: var(--shadow-md) !important;
      margin: 1.5rem 0 !important;
    }

    ul, ol {
      margin: 1rem 0 !important;
      padding-left: 2rem !important;

      li {
        margin-bottom: 0.5rem !important;
        line-height: 1.6 !important;
        //color: var(--text-primary); // 移除!important，让深色主题可以覆盖
      }
    }

    strong {
      color: var(--text-primary); // 移除!important，让深色主题可以覆盖
      font-weight: 600 !important;
    }

    em {
      color: var(--text-secondary) !important;
    }
  }
}

// 目录（TOC）美化
.toc {
  background: transparent !important;
  backdrop-filter: none !important;
  -webkit-backdrop-filter: none !important;
  border: none !important;
  border-radius: 0 !important;
  padding: 1.5rem !important;
  box-shadow: none !important;
  position: sticky !important;
  top: 5rem !important;
  margin: 2rem 0 !important; // 移除左右边距

  .toc-title {
    font-weight: 700 !important;
    margin-bottom: 1rem !important;
    //color: var(--text-primary); // 移除!important，让深色主题可以覆盖
    padding-bottom: 0.5rem !important;
    border-bottom: none !important;
  }

  .toc-content {
    ul {
      list-style: none !important;
      padding-left: 0 !important;

      li {
        margin-bottom: 0.5rem !important;

        a {
          display: block !important;
          padding: 0.5rem 1rem !important;
          border-radius: var(--border-radius-sm) !important;
          transition: all 0.3s ease !important;
          color: var(--text-secondary) !important;
          text-decoration: none !important;

          &:hover,
          &.active {
            background: var(--primary-gradient) !important;
            color: white !important;
            transform: translateX(4px) !important;
          }
        }

        ul {
          padding-left: 1rem !important;
          margin-top: 0.5rem !important;
        }
      }
    }
  }
}

// 标签样式美化
.tag {
  background: var(--primary-gradient) !important;
  color: white !important;
  border-radius: 20px !important;
  padding: 4px 12px !important;
  font-size: 0.8rem !important;
  font-weight: 500 !important;
  text-decoration: none !important;
  display: inline-block !important;
  margin: 2px 4px !important;
  transition: all 0.3s ease !important;

  &:hover {
    transform: translateY(-1px) !important;
    box-shadow: var(--shadow-md) !important;
    opacity: 0.9 !important;
  }
}

// 分页按钮美化
.pagination {
  margin: 3rem 0 !important; // 移除左右边距

  .page-number,
  .page-link {
    background: transparent !important;
    backdrop-filter: none !important;
    -webkit-backdrop-filter: none !important;
    border: 2px solid var(--primary-color) !important;
    border-radius: var(--border-radius-sm) !important;
    color: var(--text-primary); // 移除!important，让深色主题可以覆盖
    padding: 8px 10px !important;
    margin: 0 0px !important;
    transition: all 0.3s ease !important;
    text-decoration: none !important;

    &:hover,
    &.current {
      background: var(--primary-gradient) !important;
      color: white !important;
      border-color: transparent !important;
      transform: translateY(-2px) !important;
      box-shadow: none !important;
    }
  }
}

// 评论区美化
.comment {
  background: transparent !important;
  backdrop-filter: none !important;
  -webkit-backdrop-filter: none !important;
  border: none !important;
  border-radius: 0 !important;
  padding: 2rem !important;
  box-shadow: none !important;
  margin: 2rem 0 !important; // 移除左右边距

  .comment-title {
    font-size: 1.5rem !important;
    font-weight: 700 !important;
    margin-bottom: 1.5rem !important;
    color: var(--text-primary); // 移除!important，让深色主题可以覆盖
    padding-bottom: 0.5rem !important;
    border-bottom: none !important;
  }

  // Valine评论系统美化
  .valine {
    .vwrap {
      background: var(--bg-secondary) !important;
      border-radius: var(--border-radius-sm) !important;
      padding: 1rem !important;
      margin-bottom: 1rem !important;

      .veditor {
        background: var(--bg-primary) !important;
        border: 2px solid var(--glass-border) !important;
        border-radius: var(--border-radius-sm) !important;
        color: var(--text-primary) !important;

        &:focus {
          border-color: var(--primary-color) !important;
          box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1) !important;
        }
      }

      .vcontrol .vsubmit {
        background: var(--primary-gradient) !important;
        border: none !important;
        border-radius: var(--border-radius-sm) !important;
        color: white !important;
        padding: 8px 20px !important;
        font-weight: 600 !important;
        transition: all 0.3s ease !important;

        &:hover {
          transform: translateY(-1px) !important;
          box-shadow: var(--shadow-md) !important;
          opacity: 0.9 !important;
        }
      }
    }

    .vlist {
      .vcard {
        background: var(--bg-secondary) !important;
        border-radius: var(--border-radius-sm) !important;
        padding: 1rem !important;
        margin-bottom: 1rem !important;
        border: 1px solid var(--glass-border) !important;

        .vhead {
          .vnick {
            color: var(--primary-color) !important;
            font-weight: 600 !important;
          }

          .vsys {
            background: var(--primary-gradient) !important;
            color: white !important;
            border-radius: 12px !important;
            padding: 2px 8px !important;
            font-size: 0.7rem !important;
          }
        }

        .vcontent {
          color: var(--text-primary) !important;
          margin: 0.5rem 0 !important;
        }
      }
    }
  }
}

// 底部美化
.footer {
  background: transparent !important;
  backdrop-filter: none !important;
  -webkit-backdrop-filter: none !important;
  border-top: none !important;
  margin-top: 3rem !important;
  padding: 2rem 1rem !important; // 增加左右内边距
  overflow: visible !important; // 确保内容完全显示
  min-height: auto !important;
  height: auto !important;

  .footer-line {
    color: var(--text-secondary) !important;
    font-weight: 400 !important;
    line-height: 1.6 !important;
    overflow: visible !important;
    white-space: normal !important;
    word-wrap: break-word !important;
  }

  // 底部链接样式
  a {
    color: var(--primary-color) !important;
    font-weight: 500 !important;
    text-decoration: none !important;

    &:hover {
      color: var(--secondary-color) !important;
    }
  }

  // 确保所有底部内容都能正常显示
  .footer-container {
    overflow: visible !important;
    max-width: 100% !important;
  }
}

// 搜索框美化
.search-input {
  background: var(--bg-primary) !important;
  backdrop-filter: blur(20px) saturate(180%) !important;
  -webkit-backdrop-filter: blur(20px) saturate(180%) !important;
  border: 2px solid var(--glass-border) !important;
  border-radius: var(--border-radius-sm) !important;
  padding: 12px 16px !important;
  transition: all 0.3s ease !important;
  color: var(--text-primary) !important;

  &:focus {
    border-color: var(--primary-color) !important;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1) !important;
  }

  &::placeholder {
    color: var(--text-secondary) !important;
  }
}

// 社交图标美化
.social-links {
  .social-link {
    background: var(--bg-tertiary) !important;
    border-radius: 50% !important;
    width: 40px !important;
    height: 40px !important;
    display: inline-flex !important;
    align-items: center !important;
    justify-content: center !important;
    margin: 0 8px !important;
    transition: all 0.3s ease !important;
    color: var(--text-secondary) !important;

    &:hover {
      background: var(--primary-gradient) !important;
      color: white !important;
      transform: translateY(-2px) !important;
      box-shadow: var(--shadow-md) !important;
    }
  }
}

// 代码块美化
.highlight {
  border-radius: var(--border-radius) !important;
  overflow: hidden !important;
  box-shadow: var(--shadow-md) !important;
  margin: 1.5rem 0 !important;

  pre {
    border-radius: 0 !important;
  }
}

// 引用块美化 - 添加文字颜色设置
blockquote {
  border-left: 4px solid var(--primary-color) !important;
  background: var(--bg-secondary) !important;
  border-radius: 0 var(--border-radius-sm) var(--border-radius-sm) 0 !important;
  padding: 1rem 1.5rem !important;
  margin: 1.5rem 0 !important;
  position: relative !important;
  color: var(--text-primary) !important; // 添加文字颜色

  // 确保所有子元素都有正确的颜色
  p, span, div, text, strong, em {
    color: var(--text-primary) !important;
  }

  &::before {
    content: '"' !important;
    font-size: 3rem !important;
    color: var(--primary-color) !important;
    position: absolute !important;
    top: -10px !important;
    left: 10px !important;
    opacity: 0.3 !important;
  }
}

// 表格美化
table {
  border-radius: var(--border-radius) !important;
  overflow: hidden !important;
  box-shadow: var(--shadow-md) !important;

  th {
    background: var(--primary-gradient) !important;
    color: white !important;
  }

  tr:nth-child(even) {
    background: var(--bg-secondary) !important;
  }
}

// 按钮美化
.btn,
button {
  background: var(--primary-gradient) !important;
  border: none !important;
  border-radius: var(--border-radius-sm) !important;
  color: white !important;
  padding: 12px 24px !important;
  font-weight: 600 !important;
  transition: all 0.3s ease !important;
  cursor: pointer !important;

  &:hover {
    transform: translateY(-2px) !important;
    box-shadow: var(--shadow-md) !important;
    opacity: 0.9 !important;
  }
}

// 响应式优化
@media (max-width: 480px) {
  .wrapper {
    padding: 0 0.25rem !important;
    max-width: 100% !important;
  }

  .container {
    padding: 0 0.25rem !important;
    max-width: 100% !important;
  }
}

@media (max-width: 768px) {
  .wrapper {
    padding: 0 0.5rem !important;
    max-width: 100% !important;
  }

  .container {
    padding: 0 0.5rem !important;
    max-width: 100% !important;
  }

  .header .header-wrapper {
    padding: 0 1rem !important;
  }

  .home .profile {
    padding: 2rem 1rem;
    margin-bottom: 2rem;

    .profile-title {
      font-size: 2rem;
    }

    .profile-subtitle {
      font-size: 1rem;
    }
  }

  .summary {
    margin: 0 0 2rem 0 !important;

    .summary-content {
      padding: 1.5rem 1.5rem 1rem 1.5rem !important;
    }

    .post-footer {
      padding: 1rem 1.5rem 1.5rem 1.5rem !important;
    }
  }

  .single {
    .single-header {
      padding: 1.5rem !important;
      margin: 1rem 0 !important;

      .single-title {
        font-size: 2rem !important;
      }
    }

    .content {
      padding: 1.5rem !important;
      margin: 1rem 0 !important;
    }
  }

  .toc {
    position: static !important;
    margin: 1rem 0 2rem 0 !important;
  }

  .comment {
    margin: 1rem 0 !important;
    padding: 1.5rem !important;
  }

  .pagination {
    margin: 2rem 0 !important;
  }
}

@media (max-width: 1024px) {
  .wrapper {
    max-width: 95% !important;
  }

  .container {
    max-width: 90% !important;
  }

  .home .container {
    max-width: 85% !important;
  }
}

@media (min-width: 1025px) and (max-width: 1399px) {
  .wrapper {
    max-width: 1200px !important;
  }

  .container {
    max-width: 1000px !important;
  }

  .home .container {
    max-width: 900px !important;
  }
}

