// 关于页面美化
.page.single {
  // 关于页面特定样式
  &[data-about="true"] {
    .single-header {
      text-align: center !important;
      background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%) !important;
      border-radius: 25px !important;
      padding: 3rem 2rem !important;
      margin-bottom: 3rem !important;
      position: relative !important;
      overflow: hidden !important;

      &::before {
        content: '' !important;
        position: absolute !important;
        top: -50% !important;
        left: -50% !important;
        width: 200% !important;
        height: 200% !important;
        background: linear-gradient(45deg, transparent, rgba(102, 126, 234, 0.05), transparent) !important;
        animation: shimmer 3s ease-in-out infinite !important;
      }

      .single-title {
        font-size: 3.5rem !important;
        background: var(--primary-gradient) !important;
        background-clip: text !important;
        -webkit-background-clip: text !important;
        -webkit-text-fill-color: transparent !important;
        margin-bottom: 1rem !important;
        position: relative !important;
        z-index: 2 !important;
      }
    }

    .content {
      // 个人介绍区域美化
      h2 {
        display: flex !important;
        align-items: center !important;
        gap: 1rem !important;
        font-size: 2.2rem !important;
        color: var(--primary-color) !important;
        margin: 3rem 0 2rem 0 !important;
        padding: 1.5rem 2rem !important;
        background: rgba(102, 126, 234, 0.05) !important;
        border-radius: 15px !important;
        border-left: 5px solid var(--primary-color) !important;
        position: relative !important;

        &::before {
          content: '🚀' !important;
          font-size: 1.8rem !important;
          margin-right: 0.5rem !important;
        }
      }

      h3 {
        display: flex !important;
        align-items: center !important;
        gap: 0.8rem !important;
        font-size: 1.8rem !important;
        color: var(--secondary-color) !important;
        margin: 2.5rem 0 1.5rem 0 !important;
        padding: 1rem 1.5rem !important;
        background: rgba(118, 75, 162, 0.05) !important;
        border-radius: 12px !important;
        border-left: 4px solid var(--secondary-color) !important;

        &::before {
          content: '⭐' !important;
          font-size: 1.4rem !important;
        }
      }

      h4 {
        display: flex !important;
        align-items: center !important;
        gap: 0.6rem !important;
        font-size: 1.5rem !important;
        color: #e67e22 !important;
        margin: 2rem 0 1rem 0 !important;
        padding: 0.8rem 1.2rem !important;
        background: rgba(230, 126, 34, 0.05) !important;
        border-radius: 10px !important;
        border-left: 3px solid #e67e22 !important;

        &::before {
          content: '💡' !important;
          font-size: 1.2rem !important;
        }
      }

      // 段落美化
      p {
        font-size: 1.1rem !important;
        line-height: 1.8 !important;
        margin-bottom: 1.5rem !important;
        padding: 1rem 1.5rem !important;
        background: rgba(248, 250, 252, 0.8) !important;
        border-radius: 12px !important;
        border: 1px solid rgba(226, 232, 240, 0.5) !important;
        transition: all 0.3s ease !important;

        &:hover {
          background: rgba(255, 255, 255, 0.9) !important;
          border-color: rgba(102, 126, 234, 0.3) !important;
          transform: translateY(-2px) !important;
          box-shadow: 0 4px 12px rgba(102, 126, 234, 0.1) !important;
        }
      }

      // 列表美化
      ul {
        background: rgba(248, 250, 252, 0.6) !important;
        border-radius: 15px !important;
        padding: 1.5rem 2rem !important;
        margin: 1.5rem 0 !important;
        border: 1px solid rgba(226, 232, 240, 0.5) !important;

        li {
          position: relative !important;
          padding: 0.8rem 0 0.8rem 2rem !important;
          margin-bottom: 0.5rem !important;
          border-radius: 8px !important;
          transition: all 0.3s ease !important;
          list-style: none !important;

          &::before {
            content: '▶' !important;
            position: absolute !important;
            left: 0 !important;
            top: 50% !important;
            transform: translateY(-50%) !important;
            color: var(--primary-color) !important;
            font-size: 0.8rem !important;
          }

          &:hover {
            background: rgba(102, 126, 234, 0.08) !important;
            transform: translateX(8px) !important;
            padding-left: 2.5rem !important;

            &::before {
              color: var(--secondary-color) !important;
              transform: translateY(-50%) scale(1.2) !important;
            }
          }

          // 链接样式
          a {
            color: var(--primary-color) !important;
            text-decoration: none !important;
            font-weight: 500 !important;
            transition: all 0.3s ease !important;

            &:hover {
              color: var(--secondary-color) !important;
              text-decoration: underline !important;
            }
          }
        }
      }

      // 强调文本美化
      strong {
        background: var(--primary-gradient) !important;
