  }

  // 年份分组标题
  .group-title {
    font-size: 1.8rem !important;
    font-weight: 700 !important;
    color: var(--primary-color) !important;
    margin: 3rem 0 2rem 0 !important;
    padding-bottom: 0.5rem !important;
    border-bottom: 2px solid rgba(102, 126, 234, 0.2) !important;
    position: relative !important;

    &::before {
      content: '' !important;
      position: absolute !important;
      left: 0 !important;
      bottom: -2px !important;
      width: 50px !important;
      height: 2px !important;
      background: var(--primary-gradient) !important;
    }
  }

  // 文章条目 - 现代列表设计
  .archive-item {
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
    padding: 1rem 0 !important;
    border-bottom: 1px solid rgba(102, 126, 234, 0.1) !important;
    transition: all 0.3s ease !important;
    position: relative !important;

    &:hover {
      padding-left: 1rem !important;
      background: rgba(102, 126, 234, 0.05) !important;
      border-radius: 8px !important;
      border-bottom-color: transparent !important;
    }

    &:hover::before {
      content: '' !important;
      position: absolute !important;
      left: 0 !important;
      top: 50% !important;
      transform: translateY(-50%) !important;
      width: 4px !important;
      height: 60% !important;
      background: var(--primary-gradient) !important;
      border-radius: 2px !important;
    }

    .archive-item-link {
    //   color: var(--text-primary) !important;
      text-decoration: none !important;
      font-weight: 600 !important;
      font-size: 1.1rem !important;
      transition: color 0.3s ease !important;
      flex: 1 !important;

      &:hover {
        color: var(--primary-color) !important;
      }
    }

    .archive-item-date {
      color: var(--text-secondary) !important;
      font-size: 0.9rem !important;
      font-weight: 500 !important;
      padding: 0.3rem 0.8rem !important;
      background: rgba(102, 126, 234, 0.1) !important;
      border-radius: 12px !important;
      min-width: fit-content !important;
    }
  }
}

// 深色主题下的所有文章页面
[data-theme="dark"] .page.archive {
  .archive-item {
    border-bottom-color: rgba(144, 205, 244, 0.1) !important;

    &:hover {
      background: rgba(144, 205, 244, 0.05) !important;
    }

    .archive-item-date {
      background: rgba(144, 205, 244, 0.1) !important;
    }
  }
}

// 响应式设计 - 仅针对所有文章页面
@media (max-width: 768px) {
  .page.archive {
    .single-title {
      font-size: 2rem !important;
    }

    .group-title {
      font-size: 1.5rem !important;
    }

    .archive-item {
      flex-direction: column !important;
      align-items: flex-start !important;
      gap: 0.5rem !important;

      .archive-item-link {
        font-size: 1rem !important;
      }
    }
  }
}
